import QRCode from "qrcodejs2";
export const qr = {
  created() {
    this.$nextTick(() => {
      this.crateQrcode();
    });
  },
  methods: {
    crateQrcode() {
      this.qr = new QRCode("qrcode", {
        width: 200,
        height: 200,
        text: "感谢老板！QAQ",
        //colorDark: "#333333", //二维码颜色
        // colorLight: "#ffffff", //二维码背景色
        render: "canvas", // 设table / canvas 默认canvas
        correctLevel: QRCode.CorrectLevel.L
      });
    },
    cleanCode() {
        this.qr = null
      this.$refs.qrcode.innerHTML = "";
    }
  }
};



